body{
  margin:0;
}
ul{list-style: none; display: flex; text-align: center;}
li{margin:0px 10px 0px 0px;}
a{text-decoration: none; cursor: help;}

.movie{
      width: 250px;
      height: auto;
      column-width:250px;
      margin:0px 0px 0px 25px;
    }
    .movie figure{
      display: inline-block;
      border:1px solid rgba(0,0,0,0.2);
      border-radius: 15px;
      margin:0;
      margin-bottom: 25px;
      padding:10px;
      box-shadow: 2px 2px 5px rgba(0,0,0,0.5);;
      background-color: #e2e2e2;
      text-align: center;
    }
    .movie figure img{
      width:100%;
      border-radius: 15px;
      overflow: hidden;
    }
.movies-title , .movies-rating {border-bottom: 1px solid; padding:10px 0px 10px 0px; margin:0;}
.genres{margin:10px 0px 10px 0px; padding:0;  justify-content: center;}
.summary{display: none; border-bottom: 1px solid;margin:0; padding:10px 0px 10px 0px;}

.scale {
  transition: all 0.3s ease-in-out;
  transform:perspective(500px) rotate(.001deg);

}
.scale:hover {
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}
